import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomOpen } from './custom-open.component';//
import { CorporateCustomer } from './corporate-customer/corporate-customer.component';//
import { Administrative } from './administrative/administrative.component';//
import { Production } from './production/production.component';//
import { NonProduction } from './non-production/non-production.component';//
/**
 * 
 * 
 *
 */
export const routes: Routes = [
    {
        path: '',
        component: CustomOpen,
        children: [
            { path: '', redirectTo: 'production', component: Production },
            { path: 'production', component: Production },
            { path: 'corporate-customer', component: CorporateCustomer },
            { path: 'administrative', component: Administrative },
            { path: 'nonProduction', component: NonProduction },
        ]
    }
]

export const CusOpenRouting: ModuleWithProviders = RouterModule.forChild(routes);
